"use client"

import HighLightedText from "./HighLightedText";
import progress from "@/public/assets/Images/Know_your_progress.png";
import compare from "@/public/assets/Images/Compare_with_others.svg";
import plan from "@/public/assets/Images/Plan_your_lessons.svg";
import Image from "next/image";
import CTAButton from "./CTAButton";
const Features = () => {
    const features = [
        {
            image: progress,
            properties: "left-0"
        },
        {
            image: compare,
            properties: "left-[300px] top-[-45px]"
        },
        {
            image: plan,
            properties: "left-[650px] bottom-[12px]"
        }
    
    ];
    return (
    <div className="w-full h-[940px] bg-[var(--pure-greys-5)] pt-[90px] pb-[90px] pl-[120px] pr-[120px] gap-[26px] flex flex-col items-center">
        <div className="w-[1200px] h-[104px] pr-55 pl-55 gap-3 flex flex-col items-center justify-center">
            <p className="font-semibold leading-11 tracking-[-2%] text-4xl text-[var(--richblack-900)] mx-auto">
                Your swiss knife for <HighLightedText text="learning any language"/>
            </p>
            <div className="w-[768px] h-[48px] flex flex-col gap-3 items-center">
                <div className="text-base font-medium leading-[24px] text-[var(--richblack-700)] mx-auto">
                    Using spin making learning multiple languages easy. with 20+ languages realistic voice-over,
                    <p className="w-full mx-auto text-center">progress tracking, custom schedule and more.</p>
                </div>
            </div>
        </div>
        <div className="w-[1102px] h-[469px] flex relative mx-auto">
            {
                features.map((element,index) => (
                    <Image src={element.image} key={index} alt={"images"} className={`absolute ${element.properties}`}/>
                ))
            }
        </div>
        <div className="w-[1200px] h-[84px] flex gap-6 pt-9 justify-center">
            <CTAButton text="Learn More" active={true}/>
        </div>
    </div>
    );
}

export default Features;